.container {
  padding: 0 3rem;
  max-width: 90rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0 5rem;
  }
}
.container-footer {
  padding-top: 50px;
  padding-bottom: 50px;
}

.grid {
  display: grid;
  place-items: center;
  gap: 5rem;
}
@media (min-width: 40em) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.25rem;
  }
  .grid-item1 {
    order: 2;
  }
  .grid-item2 {
    order: 1;
  }
}

.grid-footer {
  display: grid;
  gap: 15px;
  min-height: 21.875rem;
}
@media (min-width: 40em) {
  .grid-footer {
    grid-template-columns: repeat(2, 1fr);
    justify-items: start;
  }
  .grid-footer-item1 {
    order: 1;
  }
  .grid-footer-item2 {
    order: 2;
  }
  .grid-footer-item3 {
    order: 4;
  }
  .grid-footer-item4 {
    order: 3;
  }
}
@media (min-width: 71.875em) {
  .grid-footer {
    grid-template-columns: repeat(4, 1fr);
  }
  .grid-footer > * {
    min-height: 10rem;
  }
  .grid-footer-item1 {
    order: 1;
    align-self: center;
  }
  .grid-footer-item2 {
    order: 2;
    align-self: flex-end;
  }
  .grid-footer-item3 {
    order: 3;
    align-self: flex-end;
  }
  .grid-footer-item4 {
    order: 4;
    align-self: flex-end;
  }
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-ac-c {
  align-content: center;
}
.flex-row {
  flex-direction: row;
}
.flex-col {
  flex-direction: column;
}